"use client"

import { useEffect, useState } from "react"
import { useParams } from "next/navigation"
import {toast} from "sonner"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Progress } from "@/components/ui/progress"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Badge } from "@/components/ui/badge"
import { Skeleton } from "@/components/ui/skeleton"

import { formatDate } from "@/lib/utils"

import { useContract } from "@/context/contract-context"
import { AddProposalForm } from "./_components/add-proposal-form"

export default function CampaignPage() {
  const { id } = useParams()

const { contract, address, isConnected } = useContract()
const [campaign, setCampaign] = useState(null)
const [proposals, setProposals] = useState([])
const [loading, setLoading] = useState(true)
const [hasVoted, setHasVoted] = useState(false)
const [isRegistered, setIsRegistered] = useState(false)
const [isCreator, setIsCreator] = useState(false)
const [timeLeft, setTimeLeft] = useState("")

  useEffect(() => {
    if (contract && id) {
      fetchCampaignData()
    }
  }, [contract, id, address])

  useEffect(() => {
    if (campaign) {
      const interval = setInterval(() => {
        updateTimeLeft()
      }, 1000)
      return () => clearInterval(interval)
    }
  }, [campaign])

  const fetchCampaignData = async () => {
    try {
      setLoading(true)
      const campaignId = Number(id)

      // Fetch campaign metadata
      const metadata = await contract.campaignMetadata(campaignId)

      // Fetch proposals
      const proposalsList = await contract.getProposals(campaignId)

      // Check if user has voted
      if (address) {
        // This would need to be implemented in the contract or tracked client-side
        // const voted = await contract.hasVoted(campaignId, address)
        // setHasVoted(voted)

        // Check if user is registered
        // const registered = await contract.isRegistered(campaignId, address)
        // setIsRegistered(registered)

        // Check if user is creator
        setIsCreator(metadata.creator.toLowerCase() === address.toLowerCase())
      }

      // Format campaign data
      const campaignData = {
        id: campaignId,
        description: metadata.description,
        creator: metadata.creator,
        startTime: Number(metadata.timeFrame.startTime),
        endTime: Number(metadata.timeFrame.endTime),
        status: Number(metadata.status),
        restriction: Number(metadata.restriction),
        resultType: Number(metadata.resultType),
        maxVoters: Number(metadata.maxVoters),
      }

      setCampaign(campaignData)
      setProposals(proposalsList)
      updateTimeLeft()
    } catch (error) {
      console.error("Error fetching campaign data:", error)
      toast({
        title: "Error",
        description: "Failed to load campaign data",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  const updateTimeLeft = () => {
    if (!campaign) return

    const now = Math.floor(Date.now() / 1000)
    let timeString = ""

    if (now < campaign.startTime) {
      // Campaign hasn't started
      const diff = campaign.startTime - now
      const days = Math.floor(diff / 86400)
      const hours = Math.floor((diff % 86400) / 3600)
      const minutes = Math.floor((diff % 3600) / 60)
      const seconds = diff % 60

      timeString = `Starts in: ${days}d ${hours}h ${minutes}m ${seconds}s`
    } else if (now < campaign.endTime) {
      // Campaign is active
      const diff = campaign.endTime - now
      const days = Math.floor(diff / 86400)
      const hours = Math.floor((diff % 86400) / 3600)
      const minutes = Math.floor((diff % 3600) / 60)
      const seconds = diff % 60

      timeString = `Ends in: ${days}d ${hours}h ${minutes}m ${seconds}s`
    } else {
      // Campaign has ended
      timeString = "Campaign has ended"
    }

    setTimeLeft(timeString)
  }

  const getStatusBadge = () => {
    if (!campaign) return null

    const now = Math.floor(Date.now() / 1000)
    let status = "Created"
    let variant = "outline"

    if (now < campaign.startTime) {
      status = "Upcoming"
      variant = "outline"
    } else if (now < campaign.endTime) {
      status = "Active"
      variant = "default"
    } else {
      status = "Ended"
      variant = "destructive"
    }

    return <Badge variant={variant}>{status}</Badge>
  }

  if (loading) {
    return (
      <>
    
        <div className="container mx-auto px-4 py-24">
          <Skeleton className="h-12 w-3/4 mb-4" />
          <Skeleton className="h-6 w-1/2 mb-8" />
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            <Skeleton className="h-64" />
            <Skeleton className="h-64" />
          </div>
        </div>
        {/* <Footer /> */}
      </>
    )
  }

  if (!campaign) {
    return (
      <>
        <Navbar />
        <div className="container mx-auto px-4 py-24 text-center">
          <h1 className="text-3xl font-bold mb-4">Campaign Not Found</h1>
          <p className="mb-8">The campaign you're looking for doesn't exist or has been removed.</p>
          <Button asChild>
            <a href="/campaigns">Back to Campaigns</a>
          </Button>
        </div>
        <Footer />
      </>
    )
  }

  return (
    <>
    
      <div className="container mx-auto px-4 py-24">
        <div className="flex flex-col md:flex-row justify-between items-start mb-8">
          <div>
            <div className="flex items-center gap-2 mb-2">
              <h1 className="text-3xl font-bold">{campaign.description}</h1>
              {getStatusBadge()}
            </div>
            <p className="text-muted-foreground mb-2">
              Created by: {campaign.creator.substring(0, 6)}...{campaign.creator.substring(38)}
            </p>
            <div className="flex flex-col sm:flex-row gap-4 text-sm text-muted-foreground">
              <span>Start: {formatDate(campaign.startTime * 1000)}</span>
              <span>End: {formatDate(campaign.endTime * 1000)}</span>
            </div>
          </div>
          <div className="mt-4 md:mt-0">
            <p className="text-lg font-semibold">{timeLeft}</p>
          </div>
        </div>

        <Tabs defaultValue="vote" className="w-full">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="vote">Vote</TabsTrigger>
            <TabsTrigger value="proposals">Proposals</TabsTrigger>
            <TabsTrigger value="results">Results</TabsTrigger>
            <TabsTrigger value="manage" disabled={!isCreator}>
              Manage
            </TabsTrigger>
          </TabsList>

          <TabsContent value="vote" className="mt-4">
            {!isConnected ? (
              <Card>
                <CardHeader>
                  <CardTitle>Connect Wallet to Vote</CardTitle>
                  <CardDescription>You need to connect your wallet to participate in this campaign.</CardDescription>
                </CardHeader>
                <CardFooter>
                  <Button className="w-full">Connect Wallet</Button>
                </CardFooter>
              </Card>
            ) : campaign.restriction === VotingRestriction.RequiredRegistration && !isRegistered ? (
              <RegisterForm campaignId={campaign.id} onSuccess={() => setIsRegistered(true)} />
            ) : (
              <VoteForm
                campaignId={campaign.id}
                proposals={proposals}
                hasVoted={hasVoted}
                isActive={campaign.status === CampaignStatus.Active}
                onVoteSuccess={() => {
                  setHasVoted(true)
                  fetchCampaignData()
                }}
              />
            )}
          </TabsContent>

          <TabsContent value="proposals" className="mt-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {proposals.map((proposal, index) => (
                <Card key={index}>
                  <CardHeader>
                    <CardTitle>Proposal #{index + 1}</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <p>{proposal.content}</p>
                  </CardContent>
                  <CardFooter>
                    <div className="w-full">
                      <div className="flex justify-between mb-1">
                        <span>Votes: {proposal.voteCount.toString()}</span>
                        <span>
                          {proposals.reduce((acc, p) => acc + Number(p.voteCount), 0) > 0
                            ? `${((Number(proposal.voteCount) / proposals.reduce((acc, p) => acc + Number(p.voteCount), 0)) * 100).toFixed(1)}%`
                            : "0%"}
                        </span>
                      </div>
                      <Progress
                        value={
                          proposals.reduce((acc, p) => acc + Number(p.voteCount), 0) > 0
                            ? (Number(proposal.voteCount) /
                                proposals.reduce((acc, p) => acc + Number(p.voteCount), 0)) *
                              100
                            : 0
                        }
                      />
                    </div>
                  </CardFooter>
                </Card>
              ))}
            </div>
          </TabsContent>

          <TabsContent value="results" className="mt-4">
            <CampaignResults
              campaignId={campaign.id}
              proposals={proposals}
              resultType={campaign.resultType}
              hasEnded={campaign.status === CampaignStatus.Ended}
            />
          </TabsContent>

          <TabsContent value="manage" className="mt-4">
            {isCreator && campaign.status === CampaignStatus.Created && (
              <AddProposalForm
               campaignId={campaign.id} onSuccess={() => fetchCampaignData()} />
            )}
          </TabsContent>
        </Tabs>
      </div>
      <Footer />
    </>
  )
}

