"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Loader2 } from "lucide-react"
import { useContract } from "@/context/contract-context"
import { toast } from "sonner"


export function AddProposalForm({ campaignId, onSuccess }) {

  const { contract } = useContract()
  const [proposalContent, setProposalContent] = useState("")
  const [isSubmitting, setIsSubmitting] = useState(false)

  const handleAddProposal = async () => {
    if (!proposalContent.trim()) {
      toast({
        title: "Empty Proposal",
        description: "Please enter proposal content",
        variant: "destructive",
      })
      return
    }

    try {
      setIsSubmitting(true)

      // Call contract method to add proposal
      const tx = await contract.addProposal(campaignId, proposalContent)

      toast({
        title: "Transaction Submitted",
        description: "Your proposal addition transaction has been submitted",
      })

      // Wait for transaction to be mined
      await tx.wait()

      toast({
        title: "Proposal Added",
        description: "Your proposal has been successfully added to the campaign",
      })

      setProposalContent("")
      onSuccess()
    } catch (error) {
      console.error("Error adding proposal:", error)
      toast({
        title: "Transaction Failed",
        description: error.message || "Failed to add proposal",
        variant: "destructive",
      })
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Add New Proposal</CardTitle>
        <CardDescription>
          Add a new proposal to your campaign. You can only add proposals before voting begins.
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          <Input
            placeholder="Enter proposal content"
            value={proposalContent}
            onChange={(e) => setProposalContent(e.target.value)}
          />
        </div>
      </CardContent>
      <CardFooter>
        <Button onClick={handleAddProposal} className="w-full" disabled={!proposalContent.trim() || isSubmitting}>
          {isSubmitting ? (
            <>
              <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              Adding Proposal...
            </>
          ) : (
            "Add Proposal"
          )}
        </Button>
      </CardFooter>
    </Card>
  )
}

